// Light Theme
@import "./bootstrap.scss";
@import "./app.scss";
.loader.show {
	display: flex;
}
@font-face {
	font-family: "Google Sans";
	src: url(../fonts/Roboto-Regular.ttf);
}

body {
	font-family: "Google Sans" !important;
	background-color: #fff !important;
}

.loader {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba($white, 0.5);
	z-index: 999999999;
	display: none;
	align-items: center;
	justify-content: center;
}

.app-loader {
	background: rgba(0, 0, 0, 0.5);
	display: none;
	height: 100%;
	text-align: center;
	position: fixed;
	width: 100%;
	z-index: 999999;
}

.app-loader img {
	left: 0;
	position: absolute;
	top: 40%;
	width: 145px;
	right: 0;
	margin: auto;
}

.header .blog-name {
    font-weight: bold;
    font-size: 1.5rem;
    color: #fff;
}

.header .blog-name a {
    color: #fff;
}


.profile-section .profile-image {
    max-width: 160px;
	height: 160px;
    width: 160px;
}
.rounded-circle {
    border-radius: 50% !important;
}

.py-2 {
    padding-top: .5rem !important;
    padding-bottom: .5rem !important;
}

.social-list a {
	width: 32px;
	color: #54B689 !important;
    height: 32px;
	padding: 5px 0px 0px 0px !important;
    display: inline-block;
    text-align: center;
    border-radius: 50%;
    transition: all 0.4s ease-in-out;
    background-color: #fff;
}

.header .nav-item .nav-link {
    color: rgba(255,255,255,0.8);
}
.header .nav-item {
    font-weight: bold;
}

.header .navbar-nav{
	display: flex;
    width: 70%;
    margin: 0 auto;
}

.header .nav-item .nav-link.active {
    color: rgba(0,0,0,0.5);
}
.me-2 {
    margin-right: .5rem !important;
}
.text-start {
    text-align: left !important;
}
.flex-column {
    -webkit-flex-direction: column !important;
    flex-direction: column !important;
}

.cursor-pointer {cursor: pointer;}

.main-content {
    margin-left: 280px;
    overflow: hidden;
}

.btn-primary {
    color: #fff;
}

.btn-primary {
    background-color: #54B689 !important; 
    border-color: #54B689 !important;
}

.single-col-max-width {
	max-width: 820px;
  }

  .cta-section .heading {
    font-size: 2rem;
    font-weight: bold;
}

.nav-tabs-custom {
	// width: 50%;
    // width: 10%;
    margin: 0 auto;
	border-bottom : 0px !important;
	margin-top: 50px;
	margin-bottom: 20px;
}

.nav-tabs-custom .nav-item .nav-link::after {
	height: 3px !important;
}

.fa-react {
    color: #62D4FA;
	width: 1.875em;
}

.fa-node-js, .fa-mongo{
	color: #54B689;
	width: 1.875em;
}

.fa-mongo{
	width: 10.875em !important;
}

.fa-angular {
    color: #CA3327;
}

.fa-tech-width{
	width: 1.875em;
}

.fa-upwork, .fa-linkedin-in, .fa-github-alt, .fa-stack-overflow{
    width: 1.2857142857em;
    text-align: center;
}

.text-muted{
    display: -webkit-box !important;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  white-space: normal;
  text-overflow:ellipsis;
  overflow:hidden;
}

.heade-content p{
    margin-bottom: 11px;
}

.project-card .card-body{
    height: 180px ;
}